<template>
  <div class="table-component">
    <el-table
      :height="tableHeight"
      ref="multipleTable"
      :data="tableDatas"
      fit
      tooltip-effect="dark"
      style="width: 100%"
      header-row-class-name="item-table-header"
      header-cell-class-name="item-table-header-cell"
      row-class-name="item-table-row"
      cell-class-name="item-table-row-cell"
      @selection-change="handleSelectionChange"
      @row-click="rowClick"
    >
      <el-table-column
        v-if="tableOptions.selection"
        type="selection"
        align="center"
        header-align="center"
        width="55"
      ></el-table-column>
      <!-- <el-table-column sortable label="工位编号">
          <template slot-scope="scope">{{ scope.row.ca01 }}</template>
      </el-table-column>-->
      <template v-for="(item,index) in tableColumns">
        <el-table-column
          :key="index"
          :sortable="item.sortable"
          :label="item.label"
          :min-width="item.width ? item.width : 'auto'"
        >
          <template slot-scope="{$index,row}">
            <div v-if="item.render" v-html="item.render(row)"></div>
            <template v-else>{{ row[item.prop] }}</template>
          </template>
        </el-table-column>
      </template>
      <el-table-column v-if="tableOptions.showActions" label="操作">
        <template slot-scope="scope">
          <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text">确定</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import * as math from "mathjs";

export default {
  props: {
    tableOptions: {
      type: Object,
      // 对象或数组默认值必须从一个工厂函数获取
      default: function() {
        return {
          height: "100px",
          selection: true,
          showActions: true
        };
      }
    },
    tableColumns: {
      type: Array,
      // 对象或数组默认值必须从一个工厂函数获取
      default: function() {
        return [];
      }
    },
    tableDatas: {
      type: Array,
      // 对象或数组默认值必须从一个工厂函数获取
      default: function() {
        return [];
      }
    }
  },
  data() {
    return {
      multipleSelection: []
    };
  },
  computed: {
    tableHeight() {
      const basicWidth = 1440;
      const val = math.round(
        (parseFloat(this.tableOptions.height) * 100) / basicWidth,
        5
      );
      return `${val}vw`;
    }
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    rowClick(row, column, event) {
      console.log(`选择`, row);
    },
    deleteRow(index, rows) {
      console.log(`删除`, index, rows);
    }
  }
};
</script>

<style lang="scss">
.table-component {
  .el-table {
    .item-table-header {
      height: 42px;
    }
    .item-table-header-cell {
      // height: vw(50px);
      font-size: 14px;
      font-weight: bold;
      color: #151c22;
      padding: 0;
      // padding-bottom: 10px;
      border-bottom: 1px solid #d8dbdf;
    }
    .item-table-row {
      height: 40px;
    }
    .item-table-row-cell {
      // height: vw(40px);
      font-size: 14px;
      font-weight: 400;
      color: #151c22;
      padding: 0;
      border-bottom: 1px solid #d8dbdf;
    }
    .caret-wrapper {
      height: 22px !important;
      width: 12px !important;
      .sort-caret {
        left: 6px;
      }
      .sort-caret.ascending {
        top: -2px;
      }
      .sort-caret.descending {
        bottom: -2px;
      }
    }
    .current-row {
      .item-table-row-cell {
        background: #f2faff;
        font-size: 14px;
        font-weight: 400;
        color: rgba(0, 168, 255, 1);
      }
    }
    &::before {
      height: 0;
    }
  }
}
</style>