<template>
  <div class="search">
    <div class="condition">
      <label class="condition-title">指令类型</label>
      <el-select class="condition-select" v-model="instructionType" clearable placeholder="请选择">
        <el-option
          v-for="item in instructionList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <div class="condition">
      <label class="condition-title">请求方</label>
      <el-select class="condition-select" v-model="requester" clearable placeholder="请选择">
        <el-option
          v-for="item in requesterList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <div class="condition">
      <label class="condition-title">工厂</label>
      <el-select class="condition-select" v-model="factory" clearable placeholder="请选择">
        <el-option
          v-for="item in factoryList"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
    <el-button class="action-button" type="primary">执行</el-button>
    <div
      v-for="(select,index) in dynamicSelect"
      :key="index"
      :class="['condition',select.className ? select.className : '']"
    >
      <label class="condition-title">{{select.label}}</label>
      <el-select
        class="condition-select"
        v-model="select.value"
        :multiple="select.multiple"
        clearable
        :placeholder="select.placeholder"
      >
        <el-option
          v-for="item in select.options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      instructionType: "one",
      requester: "",
      factory: "",
      instructionList: [
        {
          value: "one",
          label: "普通站指令"
        },
        {
          value: "two",
          label: "存储站指令"
        },
        {
          value: "three",
          label: "主轨指令"
        },
        {
          value: "four",
          label: "筛选站点指令"
        },
        {
          value: "five",
          label: "配对站指令"
        },
        {
          value: "six",
          label: "开始配对"
        }
      ],
      requesterList: [
        {
          value: "1",
          label: "请求方1"
        },
        {
          value: "2",
          label: "请求方2"
        }
      ],
      factoryList: [
        {
          value: "1",
          label: "鹏泰智能工厂"
        },
        {
          value: "2",
          label: "测试工厂"
        }
      ],
      dynamicSelect: [],
      dynamicData: {
        one: [
          {
            code: "processingUnit",
            label: "处理单元",
            value: "",
            placeholder: "处理单元编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          },
          {
            code: "startSite",
            label: "开始站点",
            value: "",
            placeholder: "开始站点编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          },
          {
            code: "targetSite",
            label: "目标站点",
            value: "",
            placeholder: "目标站点编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          }
        ],
        two: [
          {
            code: "processingUnit2",
            label: "处理单元",
            value: "",
            placeholder: "处理单元编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          },
          {
            code: "startSite2",
            label: "开始站点",
            value: "",
            placeholder: "开始站点编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          }
        ],
        three: [
          {
            code: "targetSite3",
            label: "目标站点",
            value: "",
            placeholder: "目标站点编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          }
        ],
        four: [
          {
            code: "processingUnit4",
            label: "处理单元(多选)",
            value: [],
            placeholder: "处理单元编码",
            options: [],
            multiple: true,
            dataUrl: "xxx",
            className: "grid-column-1-3"
          }
        ],
        five: [
          {
            code: "processingUnit5",
            label: "主处理单元",
            value: "",
            placeholder: "主处理单元编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          },
          {
            code: "processingUnit6",
            label: "辅处理单元(多选)",
            value: [],
            placeholder: "辅处理单元编码合集",
            options: [],
            multiple: true,
            dataUrl: "xxx",
            className: "grid-column-2-4"
          }
        ],
        six: [
          {
            code: "processingUnit7",
            label: "主处理单元",
            value: "",
            placeholder: "主处理单元编码",
            options: [],
            multiple: false,
            dataUrl: "xxx"
          },
          {
            code: "processingUnit8",
            label: "辅处理单元(多选)",
            value: [],
            placeholder: "辅处理单元编码合集",
            options: [],
            multiple: true,
            dataUrl: "xxx",
            className: "grid-column-2-4"
          }
        ]
      }
    };
  },
  computed: {},
  watch: {
    instructionType: {
      handler(newValue, oldValue) {
        this.dynamicSelect = [];
        this.dynamicData[newValue].forEach(async (item, index) => {
          if (this.$lodash.isEmpty(item.options)) {
            item.options = await this.getSelectOption();
            // let hejing = await this.getSelectOption();
            // console.log(hejing);
            // item.options = [...hejing];
          }
          this.dynamicSelect.push(item);
        });
      },
      immediate: true
    }
  },
  mounted() {
    this.getInstructionList();
  },
  methods: {
    getSelectOption() {
      return new Promise((resolve, reject) => {
        resolve([
          {
            value: "1",
            label: "模拟返回数据1"
          },
          {
            value: "2",
            label: "模拟返回数据2"
          },
          {
            value: "3",
            label: "模拟返回数据3"
          }
        ]);
      });
    },
    async getInstructionList() {
      const url = "/processUnit/listByFactoryId";
      const params = { factoryId: 2 };
      const responseData = await this.$http.get(url, {
        params
      });
      // console.log(responseData);
      if (responseData) {
        const data = responseData.data;
        console.log(data);
      }
    }
  }
};
</script>

<style lang="scss">
.search {
  //   height: 132px;
  padding: 15px 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr) 70px;
  grid-template-rows: 38px 38px;
  grid-gap: 26px 32px;
  //   align-items: center;
  place-items: center start;

  .condition {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background: #eee;

    .condition-title {
      display: inline-block;
      // width: 30%;
      font-size: 13px;
      font-weight: normal;
      color: #999990;
    }

    .condition-select {
      &.el-select {
        width: 70%;
        .el-input {
          input.el-input__inner {
            height: 38px !important;
            border-radius: 0;
            border: 1px solid #979797;
          }
          .el-select__caret {
            line-height: 38px !important;
          }
        }
      }
    }
  }

  .grid-column-1-3 {
    grid-column: 1 / 3;
  }

  .grid-column-2-4 {
    grid-column: 2 / 4;
  }

  .action-button {
    width: 62px;
    height: 28px;
    background: #0084e3;
    font-size: 14px;
    font-weight: normal;
    color: #fff;
    padding: 6px 15px;
    border-radius: 0;
  }
}
</style>